<template>
  <nav id="sidebar" class="sidebar">
    <div class="sidebar-content">
      <a class="sidebar-brand" href="/">
        <i class="align-middle" data-feather="box"></i>
        <span class="align-middle">UGES Kurulum Takip</span>
      </a>

      <ul
        class="tree"
        style="height: calc(100vh - 70.8px);overflow: auto; background-color: #f5f9fc" ref="myul"
      >
  
         
        
      </ul>
    </div>
  </nav>
</template>


<script>
import eventlistener from "../../../Helper/eventlistener";
import state from '../../../Helper/custom_state';

export default {
  data() {
    return {
      html: "",
      tree: [],
      Usttanim: "",
      FirstName: "",
      LastName: ""
    };
  },
  created() {
    eventlistener.$on("contentsave", value => {
     let selectedelement = document.querySelector('i[data-id="'+value.selectedid+'"]');
                selectedelement.style.color = "green";
                let insertelement=document.querySelector('a[data-id="'+value.selectedid+'"]');
                console.log(insertelement);
                insertelement.dataset.serino=value.seritext;
                insertelement.dataset.ipaddress=value.ipaddress;
    });

  },
  mounted() {
    (this.FirstName = JSON.parse(localStorage.getItem("user")).FirstName),
      (this.LastName = JSON.parse(localStorage.getItem("user")).LastName);
    let myself = this;
    this.$http
      .get(`${state.ip}/api/users/getprojectsbom`)
      .then(response => {
        console.log(response);
        this.progressHesapla(response.data[0]);
        this.html = this.drawelements(response.data);
       this.tree = response.data;
        this.$refs["myul"].innerHTML = this.html;
     /*   let data=Object.keys(sessionStorage);
       for (let i = 0; i < data.length; i++) {
        document.querySelector(`ul[data-acikmi="${data[i]}"]`).classList.toggle("closed")
         
       }*/
       eventlistener.$emit("sidebarok");
        let span = document.querySelectorAll(".tree-control");
        for (let i = 0; i < span.length; i++) {
          span[i].addEventListener("click", e => {
           
            if( e.target.nextElementSibling.classList.contains('closed')){
              
               e.target.nextElementSibling.classList.remove("closed");
               e.target.classList.add("tree-control-open");
            
            }
            else{
             e.target.nextElementSibling.classList.add("closed");
              e.target.classList.remove("tree-control-open");
             
            }
           
            
            eventlistener.$emit("closecontent");
          });
        }
        let liste = document.querySelectorAll("a[name='dallink']");
        for (let i = 0; i < liste.length; i++) {
          liste[i].addEventListener("click", e => {
            myself.Usttanim = "";
            myself.searchenust(e.target.dataset.stok);
            e.target.dataset["UstTanim"] = myself.Usttanim;
            for (let i = 0; i < liste.length; i++) {
              liste[i].style.backgroundColor = "#f5f9fc";
          
            }
            e.target.style.backgroundColor = "#f1e7296e";
           
            myself.$router.push({path:"/user/data/"+e.target.dataset.id, params:{id:e.target.dataset.id} }).catch(err => {});
               eventlistener.$emit("leftclicksidebar", e.target.dataset);
          });
        }
      });
  },
  methods: {
    drawelements(rows) {
      let html = "";
      for (let i = 0; i < rows.length; i++) {
        if (rows[i].nodes.length > 0) {
          html += `<li class="has-child"><i class="fas fa-folder fa-lg" style="color:${
            rows[i].work.count !== rows[i].progress.count ? "red" : "green"
          }"></i> <a>${rows[i].Tanim}</a>
             <span class="tree-control"></span> <ul data-acikmi="${rows[i].Id}"  class="closed">${this.drawelements(
               rows[i].nodes
             )}</ul></li>`;
        } else {
          html += `<li><i data-dal="dal" data-Id="${rows[i].Id}"  class="${
            rows[i].AltYapiTakip === 1 ? "fas fa-hammer " : "fas fa-video "
          }"   style="color:${rows[i].color}"></i><a  name="dallink"
             data-id="${rows[i].Id}" 
             data-serino="${rows[i].SeriNo}"
             data-altyapitakip="${rows[i].AltYapiTakip}"
             data-serinotakip="${rows[i].SerinoTakip}"
             data-iptakip="${rows[i].IpTakip}"
             data-ipaddress="${rows[i].Ipaddress}"
             data-stok="${rows[i].Stokcode}"
             data-dal="dal"> ${rows[i].Tanim} </a></li>`;
        }
      }

      return html;
    },

    searchstokTree(element, value) {
      if (element.Stokcode === value) {
        return element;
      } else if (element.nodes != null) {
        var i;
        var result = null;
        for (i = 0; result == null && i < element.nodes.length; i++) {
          result = this.searchstokTree(element.nodes[i], value);
        }
        return result;
      }
      return null;
    },
    searchenust(Stokcode) {
      let result = this.searchstokTree(this.tree[0], Stokcode);
      if (result != null) {
        if (this.Usttanim === "") this.Usttanim = result.UstStokTanim;
        else if (result.UstStokTanim !== "-")
          this.Usttanim = this.Usttanim + " - " + result.UstStokTanim;

        this.searchenust(result.UstStok);
      }
    },

    progressHesapla(tree) {
      let subwork = tree.work ? tree.work.count : 0;
      let subProgress = tree.progress ? tree.progress.count : 0;

      for (let i = 0; i < tree.nodes.length; i++) {
        if (tree.nodes[i].nodes) {
          let { newSubWork, newSubProgress } = this.progressHesapla(
            tree.nodes[i]
          );
          subwork += newSubWork;
          subProgress += newSubProgress;
        }
      }
      if (tree.work != null) tree.work = { count: 0 };
      tree.work.count = subwork;
      if (tree.progress != null) tree.progress = { count: 0 };
      tree.progress.count = subProgress;
      subwork = subwork ? subwork : 0;
      subProgress = subProgress ? subProgress : 0;
      return { newSubWork: subwork, newSubProgress: subProgress };
    }
  }
};
</script>

<style>
.tree,
.tree ul {
  margin: 5px 0 0 5px; /* indentation */
  padding: 0;
  list-style: none;
  color: black;
  position: relative;
  width: 100%;
}

.tree ul {
  margin-left: 0.5em;
} /* (indentation/2) */

.tree:before,
.tree ul:before {
  content: "";
  display: block;
  width: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 4px;
  border-left: 1px dashed;
}

ul.tree:before {
  border-left: none;
}
.tree li.has-child > .tree-control {
  position: absolute;
  left: 0px;
  top: 6px;
  width: 15px;
  height: 15px;
  line-height: 8px;
  z-index: 0;
  display: inline-block;
  color: #f5f9fc;
  border-radius: 3px;
  background: url(http://code.stephenmorley.org/javascript/collapsible-lists/button-closed.png)
    no-repeat;
  background-size: 15px 15px;
  cursor: pointer;
}
.tree li.has-child > .tree-control-open {
  position: absolute;
  left: 0px;
  top: 6px;
  width: 20px;
  height: 20px;
  line-height: 8px;
  z-index: 0;
  display: inline-block;
  color: #f5f9fc;
  border-radius: 3px;
  background: url(http://code.stephenmorley.org/javascript/collapsible-lists/button-open.png)
    no-repeat;
  background-size: 15px 15px;
  cursor: pointer;
}

.tree li {
  margin: 0;
  padding: 0 1.5em; /* indentation + .5em */
  line-height: 2em; /* default list item's `line-height` */
  position: relative;
  width: 400px;
}
i {
  margin-left: 5px;
}
.tree li:before {
  content: "";
  display: block;
  width: 17px; /* same with indentation */
  height: 0;
  border-top: 1px dashed;
  margin-top: -1px; /* border top width */
  position: absolute;
  top: 1em; /* (line-height/2) */
  left: 4px;
}

.tree li:last-child:before {
  background: #f5f9fc; /* same with body background */
  height: auto;
  top: 1em; /* (line-height/2) */
  bottom: 0;
}
.closed {
  display: none;
}
.sidebar {
  min-width: 400px;
  max-width: 400px;
}
.sidebar-brand {
  background-color: #354052;
}
.sidebar,
.sidebar-content {
  background-color: #f5f9fc;
}
.sidebar.toggled {
  margin-left: -400px;
}
</style>