<template>
  <main class="content">
    <div id="seriolan" class="col-md-6" v-show="ifserinumber">
      <label>{{Usttanim}}</label>
      <form id="loginform">
        <template v-if="serimi">
          <div class="form-group">
            <label>Seri Numarası</label>
            <input
              type="text"
              class="form-control"
              placeholder="Seri Numarasını Giriniz"
              v-model="seritext"
              required
            />
          </div>
          <div class="form-group" v-if="ipmi">
            <label>İp Adresi</label>
            <input
              type="text"
              class="form-control"
              placeholder="İp Adresini Giriniz"
              v-model="ipaddress"
              required
            />
          </div>
        </template>
        <div class="custom-control custom-checkbox form-group" v-else>
          <input
            type="checkbox"
            class="custom-control-input"
            id="customControlAutosizing"
            v-model="altyapicheck"
          />
          <label class="custom-control-label" for="customControlAutosizing">İşlem Tamamlandı mı?</label>
        </div>
        <div class="form-group">
          <div class="custom-file">
            <input
              type="file"
              class="custom-file-input"
              ref="myFiles"
              @change="previewFiles"
              id="validatedCustomFile"
              required
            />
            <label class="custom-file-label" for="validatedCustomFile">{{value}}</label>
          </div>
          <hr />
          <div class="form-group mb-0 text-center">
            <button class="btn btn-primary mr-4" type="submit" @click="kaydet">Kaydet</button>
            <button class="btn btn-warning" type="submit" @click="ifserinumber=false">İptal</button>
          </div>
        </div>
      </form>
      <!-- end col-->
    </div>
  </main>
</template>

<script>
import eventlistener from "../../Helper/eventlistener";
import Swal from "sweetalert2";
import  state from "../../Helper/custom_state"
export default {
  data() {
    return {
      Usttanim: "",
      ifserinumber: false,
      seritext: "",
      selectedFile: "",
      ipaddress: "",
      altyapicheck: false,
      serimi: false,
      value: "",
      ipmi:false,
      selectedid: 0,
      Stokcode:""
    };
  },
  created() {
    
    eventlistener.$on("leftclicksidebar", dataset => {
     // this.solclick(this.$route.params.id);
     
    });

    eventlistener.$on("closecontent", dataset => {
      this.ifserinumber = false;
    
    });
     eventlistener.$on("sidebarok", dataset => {
      console.log(document.querySelector("a[data-id='"+this.$route.params.id+"']"));
      
     document.querySelector("a[data-id='"+this.$route.params.id+"']").style.backgroundColor = "#f1e7296e";
     this.parantac(document.querySelector("a[data-id='"+this.$route.params.id+"']"));
    
    });

     this.solclick(this.$route.params.id);
    
  },
  watch:{
    '$route':'solclick'
  },
  methods: {
    previewFiles(event) {
      this.value = this.$refs.myFiles.files[0].name;

      this.selectedFile = event.target.files[0];
    },
    parantac(element){
       
      if(element.tagName==="UL"&&!element.classList.contains("tree")){
        element.classList.toggle("closed");
         element.previousElementSibling.classList.toggle("tree-control-open");
        }
     
      else if(element.classList.contains("tree")){
           return;
      }
        this.parantac(element.parentElement);
      
    },

    solclick() {
     let id=this.$route.params.id
       this.$http
      .post(`${state.ip}/api/users/idyegorecek`,{
                id:id
          })
      .then(response => {
        // eslint-disable-next-line no-console
      
        this.ifserinumber = true;
        this.selectedid =id;
         this.Usttanim=response.data.UstTanim;
         let dataset=response.data.rowdata;
          this.Stokcode=dataset.Stokcode;
       if (dataset.SerinoTakip == "1") {
        if (dataset.IpTakip == "1") {
          this.ipmi = true;
         dataset.Ipaddress === null
            ? (this.ipaddress = "")
            : (this.ipaddress = dataset.Ipaddress);
        }
        else{
          this.ipmi = false;
        }
        this.serimi = true;
        
        dataset.SeriNo === null
          ? (this.seritext = "")
          : (this.seritext = dataset.SeriNo);
      } else if (dataset.AltYapiTakip == "1") {
        this.serimi = false;
        if (dataset.SeriNo === "true") this.altyapicheck = true;
        else this.altyapicheck = false;
      } else {
        this.ifserinumber = false;
        this.seritext = "";
      }
      });
    },
    kaydet: function(e) {
      let myself = this;
      e.preventDefault();
      const formData = new FormData();
      formData.append("Stokcode",this.Stokcode);
      if (this.serimi) {
        formData.append("serinumber", this.seritext);
        formData.append("file", this.selectedFile);
        formData.append("id", this.selectedid);
        if (this.ipmi) formData.append("ipaddress", this.ipaddress);
      } else {
        formData.append("serinumber", this.altyapicheck);
        formData.append("file", this.selectedFile);
        formData.append("id", this.selectedid);
      }
      if (
        (this.seritext !== "" && this.selectedFile !== "") ||
        (this.serimi === false && this.selectedFile !== "")
      ) {
        if (this.ValidateIPaddress(this.ipaddress)||this.serimi === false) {
          this.$http
            .post(`${state.ip}/api/users/serinokaydet`, 
              formData
            )
            .then(response => {
              if (response.data.succes) {
                 Swal.fire("", response.data.msg, "success");
                 eventlistener.$emit("contentsave", {selectedid:this.selectedid,seritext:this.seritext,ipaddress:this.ipaddress});
                 document.getElementById("loginform").reset();
                myself.value = "Resim seç";
                this.ifserinumber = false;
              }
              // result.SeriNo = myself.seritext;
              else {
                Swal.fire("", response.data.msg, "error");
              }
            })
            .catch(function(error) {
              // eslint-disable-next-line no-console
              console.log(error);
            });
        } else {
          Swal.fire("", "Geçerli bir ip adresi giriniz.", "error");
        }
      } else {
        Swal.fire("", "Tüm alanlar doldurulmalıdır.", "error");
      }
    },
     ValidateIPaddress(ipaddress) 
        {
        var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
           if(ipaddress.match(ipformat))
              {
                return true;
               }
 else
 {
 return false;
 }
        },
  }
};
</script>

